import React from 'react';

// Const & Utils
import { useWindowSize } from '../../utils/useWindowSize';
import { mediaSize } from '../../constants/theme';

// Components
import {
  Layout,
  ServiceCard,
  Container,
  Statement,
  ServiceGroup,
  Carousel,
} from '../../components';

// Data
import data from '../../data/pages/services.json';
import services from '../../data/services.json';

const Services = () => {
  const content = data.content;
  const isMobile = useWindowSize().width <= mediaSize.mobile;
  return (
    <Layout pageContext={data}>
      <Container offsetHero>
        {/* Iterate through service groups */}
        {/* Desktop version wrapped with ServiceGroup */}
        {!isMobile && (
          <>
            {Object.entries(services.groups).map(([key, value]) => (
              <ServiceGroup key={key} title={value.title}>
                {Object.values(services.data)
                  .filter((service) => service.group === key)
                  .map((service) => (
                    <ServiceCard
                      key={service.path}
                      title={service.title}
                      text={service.blurb}
                      imagePath={service.imagePath}
                      cta={{ src: service.path, label: 'Learn More' }}
                    />
                  ))}
              </ServiceGroup>
            ))}
          </>
        )}
        {/* Mobile version wrapped with Carousel */}
        {isMobile && (
          <>
            {Object.entries(services.groups).map(([key, value]) => (
              <Carousel key={key} title={value.title}>
                {Object.values(services.data)
                  .filter((service) => service.group === key)
                  .map((service) => (
                    <ServiceCard
                      key={service.path}
                      title={service.title}
                      text={service.blurb}
                      imagePath={service.imagePath}
                      cta={{ src: service.path, label: 'Learn More' }}
                    />
                  ))}
              </Carousel>
            ))}
          </>
        )}
        {/* CTA Statement*/}
        <Statement
          title={content.statement.title}
          cta={content.statement.cta}
          styleType="cta">
          <p>{content.statement.text}</p>
        </Statement>
      </Container>
    </Layout>
  );
};

export default Services;
